import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../components/Login' //引入login单组件
import Home from '../components/Home' //引入login单组件
import Welcome from '../components/Welcome' //引入login单组件
import Users from '../components/user/Users' //引入login单组件
import Rights from '../components/power/Rights' //引入login单组件
import Roles from '../components/power/Roles' //引入login单组件
import Cate from '../components/goods/Cate' //引入login单组件
import Params from '../components/goods/Params' //引入login单组件
import GoodsList from '../components/goods/List' //引入login单组件
import Add from '../components/goods/Add' //引入login单组件


import Order from '../components/order/Order' //引入login单组件
import Report from '../components/report/Report' //引入login单组件

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    { path: '/', redirect: '/login' }, //当我们访问'/'自动匹配'login'
    { path: '/login', component: Login },
    {
      path: '/home',
      component: Home,
      //重定向
      redirect: '/welcome',
      children: [
        { path: '/welcome', component: Welcome },
        { path: '/users', component: Users },
        { path: '/rights', component: Rights },
        { path: '/roles', component: Roles },
        { path: '/categories', component: Cate },
        { path: '/params', component: Params },
        { path: '/orders', component: Order },
        { path: '/reports', component: Report },
        {path: '/goods',component: GoodsList},
        {path: '/goods/add',component: Add},
        
      ],
    },
  ],
})

//挂载路由导航守卫
router.beforeEach((to, from, next) => {
  //to将要访问的路径
  //from代表从哪个路径跳转而来
  //next是一个函数 表示放行
  //next() 放行  next('/login')强制跳转

  if (to.path === '/login') return next()
  //获取token
  const tokenStr = window.sessionStorage.getItem('token')
  if (!tokenStr) return next('/login')
  next()
})
export default router //暴露
