<template>
  <el-container class="home-container">
    <!-- 头部 -->
    <el-header>
      <div>
        <img src="../assets/logo.png" alt="" />
        <span>电商后台管理系统</span>
      </div>
      <el-button type="info" @click="logout">退出</el-button>
    </el-header>
    <!-- 页面主体 -->
    <el-container>
      <!-- 侧边栏 -->
      <!-- 动态绑定侧边栏宽度 -->
      <el-aside :width="isCollapse ?'64px' : '200px'">

        <!-- 折叠效果 -->
        <div class="toggle-button" @click="toggleCollapse">|||</div>

        <!-- 侧边栏菜单区域 -->
        <!-- :unique-opened="true"是组件库提供的只打开一个导航栏的属性 添加需要加： -->
        <el-menu
          background-color="#333744"
          text-color="#fff"
          active-text-color="#409eff"
          unique-opened
          :collapse="isCollapse"
          :collapse-transition="false"
          :router="true"
          :default-active="activePath"
        >
          <!-- 一级菜单 -->
          <!-- index只接收字符串所有值为 item.id+'' -->
          <el-submenu :index="item.id+''" v-for="item in menulist" :key="item.id" >
            <!-- 一级菜单的模板区 -->
            <template slot="title">
              <!-- 图标 -->
              <!-- 此时的class进行的动态添加data中iconsObj中的图标类名 -->
              <i :class="iconsObj[item.id]"></i>
              <!-- 文本 -->
              <span>{{item.authName}}</span>
            </template>

            <!-- 二级菜单 -->
            <el-menu-item :index="'/'+subItem.path" v-for="subItem in item.children" :key="subItem.id" @click="saveNavState('/'+subItem.path)">
              <!-- 二级菜单的模板区 -->
              <template slot="title">
                <!-- 图标 -->
                <i class="el-icon-menu"></i>
                <!-- 文本 -->
                <span>{{subItem.authName}}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>

      <!-- 右侧内容主体 -->
      <el-main>
        <!-- 路由占位符 -->
        <router-view ></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      //左侧菜单栏数据,如果请求数据成功则返回值给menulist
      menulist: [],
      //外设图标，利用循环时的动态添加，对应id进行赋值不同图标
      iconsObj:{
        '125':'iconfont icon-user',
        '103':'iconfont icon-tijikongjian',
        '101':'iconfont icon-shangpin',
        '102':'iconfont icon-danju',
        '145':'iconfont icon-baobiao',
      },
      // 定义一个布尔值 设置折叠与否
      isCollapse:false,
      //被激活的布尔值
      activePath:''
    }
  },
  //立即挂载
  created() {
    this.getMenuList()
    // 当页面一挂载就把全局里的值传给activePath
    this.activePath=window.sessionStorage.getItem('activePath')
    
  },
  methods: {
    //退出登录
    logout() {
      //清空window.sessionStorage的值
      window.sessionStorage.clear()
      //跳转到登录页
      this.$router.push('/login')
    },
    //获取所有的菜单
    async getMenuList() {
      //通过访问服务器获取菜单栏数值，接口为menus( async await 为简化数据)
      const { data: res } = await this.$http.get('menus')
      // 判断访问是否成功 不成功则返回原因 成功则传递数据给data中的menulist
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.menulist = res.data
    },
    //点击按钮 切换左侧菜单栏的折叠与展开
    toggleCollapse(){
      this.isCollapse=!this.isCollapse//!可以取反
    },
    //保存链接的激活状态
    saveNavState(activePath){
      window.sessionStorage.setItem('activePath',activePath)
      this.activePath=activePath
    }
  },
}
</script>

<style lang="less" scoped>
.el-header {
  background-color: #373d41;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  div {
    display: flex;
    align-items: center;
  }
  img {
    width: 50px;
    height: 50px;
    margin-right: 15px;
  }
  color: #fff;
  font-size: 20px;
}
.el-aside {
  background-color: #333744;
  .el-menu{
    border-right: none;
  }
}
.el-main {
  background-color: #eaedf1;
}
.home-container {
  height: 100%;
}
.iconfont{
  margin-right: 10px;
}
.toggle-button{
  background-color: #4a5064;
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
</style>
